<template>
  <div id="app">
    <div class="buttons">
      <h1>Buttons</h1>
      <g-button
        name="setting" @click="showToast"
      >
        Default
      </g-button>
      <g-button
        name="setting" @click="showToast" iconPositon="left"
      >
        Default
      </g-button>
      <g-button
        name="setting" @click="showToast" disabled
      >Disabled
      </g-button>
    </div>
    <div class="inputs">
      <h1>Input</h1>
      <g-input></g-input>
      <g-input error="输入格式错误"></g-input>
    </div>
    <div class="tab">
      <h1>Tab</h1>
      <g-tab class="g-tab">
        <g-tab-head>
          <g-tab-item name="news" selected="selectedTab">新闻</g-tab-item>
          <g-tab-item name="girl">美女</g-tab-item>
          <g-tab-item name="car">汽车</g-tab-item>
        </g-tab-head>
        <g-tab-body>
          <g-tab-pane name="news">很多新闻</g-tab-pane>
          <g-tab-pane name="girl"> 很多美女</g-tab-pane>
          <g-tab-pane name="car">很多汽车</g-tab-pane>
        </g-tab-body>
      </g-tab>
    </div>

    <div class="popover">
      <h1>Popover</h1>
      <g-popover class="pop">
        <g-button
          name="setting" @click="showToast" iconPositon="left"
        >
          Default
        </g-button>
        <p slot="content">I', here</p>
      </g-popover>
    </div>

    <div class="collapse">
      <h1>
        Collapse
      </h1>
      <g-collapse :selected=selected>
        <p>分类</p>
        <g-collapse-item name="1" title="新闻"><p>很多新闻</p></g-collapse-item>
        <g-collapse-item name="2" title="美女"><p>很多美女</p></g-collapse-item>
        <g-collapse-item name="3" title="汽车"><p>很多汽车</p></g-collapse-item>
      </g-collapse>
    </div>

  </div>
</template>
<script>


  export default {
    name: 'App',

    components: {},
    methods: {
      showToast() {
        this.$toast("123", {})
      }
    },
    data() {
      return {
        selected: [1],

      }
    }

  }
</script>

<style lang="scss">
  @import url("./_initcss.sass");

  #app {
    h1 {
      font-size: 1.5em;

    }

    margin: 20px;

    .buttons {
      > * {
        margin: 10px 0;
      }
    }

    .inputs {
      > * {
        margin: 10px 0;
      }
    }
    .popover{
      > * {
        margin: 10px 0;
      }
      .pop{
        margin-left: 100px;
      }
    }
    .collapse {
      > * {
        margin: 10px 0;
      }
    }

  }
</style>



